.product-detail-container{width:100%;max-width:1200px;margin:0 auto;padding:30px 0}.product-detail-page{display:flex;flex-direction:column;width:100%;margin-bottom:60px}.product-detail-page h1{font-size:28px;font-weight:600;margin-bottom:20px;color:#333}.product-detail-page p{font-size:16px;line-height:1.5;color:#666}.related-products{margin-top:30px;width:100%}@media(max-width: 1024px){.product-detail-container{padding:20px 15px}.product-detail-page{margin-bottom:40px}.product-detail-page h1{font-size:24px}}@media(max-width: 768px){.product-detail-page{margin-bottom:30px}.product-detail-page h1{font-size:22px}}@media(max-width: 480px){.product-detail-container{padding:15px 10px}.product-detail-page{margin-bottom:20px}.product-detail-page h1{font-size:20px}.product-detail-page p{font-size:14px}}